@keyframes sliding {
  0% {
    transform: translateX(0); }
  100% {
    transform: translateX(-16.66667%); } }

.slider .slider__mask {
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 1rem 0; }
  .slider .slider__mask::before, .slider .slider__mask::after {
    content: '';
    position: absolute;
    top: 0;
    width: 2rem;
    height: 100%;
    z-index: 1; }
  .slider .slider__mask::before {
    left: 0;
    background-image: linear-gradient(to right, #fafafa, transparent 50%); }
  .slider .slider__mask::after {
    right: 0;
    background-image: linear-gradient(to right, transparent 50%, #fafafa); }

.slider .slider__wrapper {
  width: calc(25% * 6);
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  animation: sliding 20s infinite alternate-reverse linear; }

