  width: calc(100% / 3);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 0;
  position: relative; }

.slider .slider__star-checkbox {
  display: none; }
  .slider .slider__star-checkbox:checked + .slider__star-icon::before {
    opacity: 0; }
  .slider .slider__star-checkbox:checked + .slider__star-icon::after {
    opacity: 1; }

.slider .slider__star-icon {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer; }
  .slider .slider__star-icon:hover, .slider .slider__star-icon:focus {
    color: #ffa726; }
