.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 3.5rem;
    padding: 0 1rem;
    background: $white;
    box-shadow: $shadow-2;
    position: relative; // Upewnienie się, że navbar jest wyżej, niż treść podstrony. Istotne przy rozwinięciu dropdowna
    z-index: 1;

    .navbar__wrapper {
        display: flex;
        flex-direction: row;
        height: 100%; // Aby obszar klikalny był na całą wysokość navbara.

    }

    .navbar__section {
        display: flex;
        align-items: center; // Wycentrowanie linków w poziomie.
        margin-left: 1rem;
        height: inherit;
    }

}

@media (max-width: 48rem) {

    .navbar {

        .navbar__section.navbar__section--navbar-user { display: none; }

    }

}